import share from '@/components/share';
export default {
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected: [],

			info: [],
			favorite: true,
			shareList: [],
			imgList: [],
			desc: '',
			specList: [{
					id: 1,
					name: '尺寸',
				},
				{
					id: 2,
					name: '颜色',
				},
			],
			specChildList: [{
					id: 1,
					pid: 1,
					name: 'XS',
				},
				{
					id: 2,
					pid: 1,
					name: 'S',
				},
				{
					id: 3,
					pid: 1,
					name: 'M',
				},
				{
					id: 4,
					pid: 1,
					name: 'L',
				},
				{
					id: 5,
					pid: 1,
					name: 'XL',
				},
				{
					id: 6,
					pid: 1,
					name: 'XXL',
				},
				{
					id: 7,
					pid: 2,
					name: '白色',
				},
				{
					id: 8,
					pid: 2,
					name: '珊瑚粉',
				},
				{
					id: 9,
					pid: 2,
					name: '草木绿',
				},
			]
		};
	},
	async onLoad(options) {

		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		if (id) {
			//this.$api.msg(`点击了${id}`);
		}
		uni.request({
			url: 'https://www.xinghuohan.cn/xhh-wx-api/wx/goods/detail?id=' + id,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.info = res.data.data.info;

				// 处理后台返回的图片地址值
				let arr = res.data.data.info.gallery;
				let newArr = arr.map(val => {
					let json = {};
					json.src = val;
					return json;
				});
				this.imgList = newArr;//这个是页面循环的值
				
				// 处理富文本框中的内容
				let result = res.data.data.info.detail;
				const regex = new RegExp('<img', 'gi');
				result = result.replace(regex, `<img style="width: 100%;"`);
				this.desc = result;
			},
			fail: () => {},
			complete: () => {}
		});


		//规格 默认选中第一条
		this.specList.forEach(item => {
			for (let cItem of this.specChildList) {
				if (cItem.pid === item.id) {
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		this.shareList = await this.$api.json('shareList');
	},
	methods: {
		//规格弹窗开关
		toggleSpec() {
			if (this.specClass === 'show') {
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			} else if (this.specClass === 'none') {
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid) {
			let list = this.specChildList;
			list.forEach(item => {
				if (item.pid === pid) {
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = [];
			list.forEach(item => {
				if (item.selected === true) {
					this.specSelected.push(item);
				}
			})

		},
		//分享
		share() {
			this.$refs.share.toggleMask();
		},
		//收藏
		toFavorite() {
			this.favorite = !this.favorite;
		},
		buy() {
			uni.navigateTo({
				url: `/pages/order/createOrder`
			})
		},
		stopPrevent() {}
	},

}
